import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import styled from 'styled-components';
import { io } from 'socket.io-client';
import style from '../styles/styles.js';
import exit from '../assets/exit.png';

import UserCard from '../components/UserCard';

import ModalSetting from '../components/ModalSetting.js';

import Chatting from '../components/Chatting.js';

//import { useLocation } from 'react-router';
import RefreshVerification from '../server/RefreshVerification.js';
import { useHistory, useLocation } from 'react-router';
import colors from '../styles/styles.js';
import InfoSetModal from '../components/InfoSetModal.js';
import InfoModal from '../components/InfoModal.js';
import Header from '../components/Header.js';

const BaseURL = 'http://3.17.55.178:3002';

//RefreshVerification.verification();

// local storage  
let data = localStorage.getItem('token');
let save_token = JSON.parse(data) && JSON.parse(data).access_token;
let save_refresh_token = JSON.parse(data) && JSON.parse(data).refresh_token;
let save_user_idx = JSON.parse(data) && JSON.parse(data).user_idx;
let save_user_name = JSON.parse(data) && JSON.parse(data).user_name;

console.log('  : ' + save_user_idx);

// room_idx 
let room_idx = 0;

//1  
const socket = io(BaseURL, {
    auth: {
        token: save_token,
    },
});

//    
socket.on('connect', () => {
    console.log('Waiting connection server -> gameStart');
});

//    
let ready_cnt = 0;

let locationUserList = [{}]; //location    

export default function WaitingRoom({ match }) {
    let location = useLocation();
    const history = useHistory();

    const room_index = match.params.name; // url   
    console.log('  ?' + room_index);

    // 
    const [userList, setUserList] = useState();

    //
    const [colorList, setColorList] = useState([
        //     , true : 
        { color: 'RED', choose: 'true', code: '#FF0000' },
        { color: 'ORANGE', choose: 'true', code: '#FF5E00' },
        { color: 'YELLOW', choose: 'true', code: '#FFE400' },
        { color: 'GREEN', choose: 'true', code: '#1DDB16' },
        { color: 'BLUE', choose: 'true', code: '#0B37D3' },
        { color: 'PURPLE', choose: 'true', code: '#5F00FF' },
        { color: 'PINK', choose: 'true', code: '#FF00DD' },
    ]);

    const [roomEnterInfo, setRoomEnterInfo] = useState(location.state.data);
    const [roomInfo, setRoomInfo] = useState('');
    const [count, setCount] = useState('');

    //    
    const [selectColor, setSelectColor] = React.useState([]);

    //   state
    const [changeReady, setChangeReady] = React.useState(false);

    // state
    const [isLeader, setIsLeader] = React.useState(0);

    //  state       .
    const [startMember, setStartMember] = React.useState(0);

    const [gameStart, setGameStart] = React.useState(false);

    //   useState
    const [roomUpdate, setRoomUpdate] = useState();

    // 
    const [currentMember, setCurrentMember] = useState();

    // 
    const [leaderIdx, setLeaderIdx] = useState();

    const dataModifiy = async (str) => {
        console.log('================   ! ' + str);

        //, ,   

        let modifyRoomEnterInfo = roomEnterInfo;

        // 
        modifyRoomEnterInfo.waiting_room_member_list = userList && userList;

        //    ,  currentMember  .
        // 
        modifyRoomEnterInfo.room_current_member_cnt = 2;

        console.log('     : ' + modifyRoomEnterInfo.room_current_member_cnt);

        // 
        modifyRoomEnterInfo.leader_idx = leaderIdx;
        console.log('  > : ' + modifyRoomEnterInfo.leader_idx);

        //!
        setRoomEnterInfo(modifyRoomEnterInfo);
    };

    useEffect(() => {
        //  leaderIdx
        socket.on('change host', (data) => {
            console.log(' ');

            setLeaderIdx(data.user_idx);
        });
    });

    useEffect(() => {
        //
        socket.on('exit room', (data) => {
            const exitUserIdx = data.user_idx;

            console.log('> : ' + Array.isArray(userList));
            const isArr = Array.isArray(userList);
            //      true  true   .
            if (isArr === true) {
                const exitUserList = userList.filter((user) => user.user_idx !== exitUserIdx);
                //filter
                setUserList(exitUserList);
            }
            console.log('       : ' + currentMember);

            setCurrentMember(currentMember - 1);

            dataModifiy('');
        });
    });

    useEffect(() => {
        //   
        socket.on('enter room', (data) => {
            console.log(' data : ' + JSON.stringify(data));

            const user = {
                user_idx: data.user_idx,
                user_name: data.user_name,
                wrm_user_color: data.user_color,
                wrm_user_ready: false,
            };

            const isArr = Array.isArray(userList);

            //      true  true   .
            if (isArr === true) {
                const enterUserList = userList.concat(user);
                //concat 
                setUserList(enterUserList);
            }

            colorList &&
                colorList.map((element) => {
                    if (element.color === data.user_color) {
                        console.log('  :   : ' + JSON.stringify(element.color));
                        element.choose = 'false';
                    }
                });

            setColorList(colorList);

            console.log('   ? : ' + parseInt(currentMember));
            // 
            setCurrentMember(parseInt(currentMember) + 1);

            dataModifiy('');
        });
    });

    useEffect(() => {
        //   socket
        socket.on('start game', (data) => {
            alert(' ,    ' + data.game_idx);

            // ,  
            history.push({
                pathname: '/playingroom/' + room_idx,
                state: { data: data },
            });
        });
    }, [gameStart]);

    useEffect(() => {
        //      
        socket.on('change member ready', (data) => {
            const changeReadyUserIdx = data.user_idx;
            const changeReadyResult = data.user_ready;

            //userList   ready 
            const isArr = Array.isArray(userList);

            let middleReadySocket = [{}];

            console.log(' : ? ' + isArr);

            //      true  true   .
            if (isArr === true) {
                userList.forEach((element) => {
                    if (changeReadyUserIdx === element.user_idx) {
                        //element.wrm_user_color = element.wrm_user_color;
                        //element.user_name = element.user_name;
                        element.user_idx = changeReadyUserIdx;
                        element.wrm_user_ready = changeReadyResult;
                        middleReadySocket.push(element);
                    }
                });

                console.log(':  : ' + JSON.stringify(middleReadySocket));

                const concatUserReady = userList.concat(middleReadySocket);
                console.log(':  : ' + JSON.stringify(concatUserReady));

                const filterReadyUserList = concatUserReady.filter((item, pos) => userList.indexOf(item) == pos);

                //filter
                setUserList(filterReadyUserList);

                console.log(' : : ' + JSON.stringify(userList));
            }

            //    ?
            if (changeReadyResult === true) {
                ready_cnt += 1;
                console.log('! ready , ready  : ' + ready_cnt);
            } else {
                if (ready_cnt != 0) ready_cnt -= 1;
                console.log('! ready , ready  : ' + ready_cnt);
            }

            alert('socket user_idx : ' + data.user_idx + ' user_ready : ' + data.user_ready);
        });
    });

    useEffect(() => {
        //    response    
        socket.on('change member color', (data) => {
            alert('socket-> index: ' + data.user_idx + ' color: ' + data.before_color + ' color: ' + data.current_color);

            const changeColorUserIdx = data.user_idx;
            const changeUserBeforeColor = data.before_color;
            const changeUserCurrentColor = data.current_color;

            const isArr = Array.isArray(userList);
            let middleColorSocket = [{}];

            //      true  true   .
            if (isArr === true) {
                userList.forEach((element) => {
                    if (changeColorUserIdx === element.user_idx) {
                        element.wrm_user_color = changeUserCurrentColor;
                        console.log('   ? ' + element.wrm_user_color);
                        //element.user_name = element.user_name;
                        element.user_idx = changeColorUserIdx;
                        //element.wrm_user_ready = false;
                        middleColorSocket.push(element);
                    }
                });

                console.log('  : middle socket arr: ' + JSON.stringify(middleColorSocket));

                const concatUserColor = userList.concat(middleColorSocket);
                const filterColorUserList = concatUserColor.filter((item, pos) => userList.indexOf(item) == pos);

                //filter
                setUserList(filterColorUserList);

                console.log(' ? : ' + JSON.stringify(filterColorUserList));
            }

            colorList &&
                colorList.map((element) => {
                    if (element.color === changeUserCurrentColor) {
                        element.choose = 'false';
                    }
                    setColorList(colorList);

                    if (element.color === changeUserBeforeColor) {
                        element.choose = 'true';
                    }

                    setColorList(colorList);
                });

            console.log('    : ' + JSON.stringify(colorList));

            dataModifiy('');
        });
    });

    const [result, setResult] = useState(0);
    const clickedSetting = (result) => {
        setResult(result);
    };

    useEffect(() => {
        //   
        socket.on('edit room', (data) => {
            alert(') ! ');
            setRoomUpdate(data);
        });
    }, [result]);

    function readyClick(readyStatus) {
        setChangeReady(readyStatus);

        console.log('    : ' + ready_cnt + ' : ' + startMember);

        const restURL = BaseURL + '/waiting-room/user-ready   ';

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };

        axios
            .patch(
                restURL,
                {
                    room_idx: room_idx, //  .
                    user_ready: readyStatus,
                },
                reqHeaders
            )
            .then(function (response) {
                alert('rest Start ' + readyStatus);
            })
            .catch(function (error) {
                alert('error Start' + error.message);
            });
    }

    function startClick() {
        alert('click start btn!');

        const restURL = BaseURL + '/game/start';

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };

        axios
            .post(
                restURL,
                {
                    room_idx: room_idx,
                },
                reqHeaders
            )
            .then(function (response) {
                //response jwt token 
                alert('success! ');
                //  ,   call   
                setGameStart(true);
            })
            .catch(function (error) {
                alert(error);
            });
    }

    function colorClick(str) {
        alert('click: ' + str);

        const restURL = BaseURL + '/waiting-room/user-color';

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };

        axios
            .patch(
                restURL,
                {
                    room_idx: room_idx, //   .
                    user_color: str, //   
                },
                reqHeaders
            )
            .then(function (response) {
                alert('rest ' + response.data);
                setSelectColor(str); //  
            })
            .catch(function (error) {
                alert('error ' + error.message);
            });
    }

    const getRoomInfo = async () => {
        //    api
        const restURL = 'http://3.17.55.178:3002/room/info/' + location.state.data.room_idx;

        const reqHeaders = {
            headers: {
                //1  
                authorization: 'Bearer ' + save_token,
            },
        };
        axios
            .get(restURL, reqHeaders)
            .then(function (response) {
                setRoomInfo(response.data);
                console.log('response.data.room_start_member_cnt');
                console.log(response.data.room_idx);
                setCount(response.data.room_start_member_cnt);
                console.log('getRoomInfo ');
            })
            .catch(function (error) {
                console.log('getRoomInfo ');
                console.log(error.response);
            });
    };

    const deleteRoom = async () => {
        // 
        const restURL = BaseURL + '/room/' + room_idx;

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };
        axios
            .delete(restURL, reqHeaders)
            .then(function (response) {
                console.log('  ');
                history.push({
                    pathname: '/roomlist', //    
                });
            })
            .catch(function (error) {
                console.log('   ');
                console.log(error.response);
            });
    };

    const exitRoom = async () => {
        const restURL = BaseURL + '/waiting-room/exit/' + room_idx;

        const reqHeaders = {
            headers: {
                authorization: 'Bearer ' + save_token,
            },
        };
        axios
            .delete(restURL, reqHeaders)
            .then(function (response) {
                console.log(response.status);
                console.log('exitWaitingRoom ');
                history.push({
                    pathname: '/roomlist', //    
                });
            })
            .catch(function (error) {
                console.log('exitWaitingRoom ');
                console.log(error.response);
            });
    };

    const exitWaitingRoom = async () => {
        //  1  api call     api call

        if (currentMember == 1) {
            deleteRoom(); //  api
        } else {
            exitRoom(); //   api
        }
    };

    const settingRoom = async () => {
        //room index 
        room_idx = roomEnterInfo.room_idx; //  number api,   X\

        //  , save_user_idx     
        //   state 
        if (roomEnterInfo.leader_idx === save_user_idx) {
            setIsLeader(1); //
        }

        //userlist       
        locationUserList = roomEnterInfo.waiting_room_member_list;
        //setUserList(location.state.data);
        setUserList(locationUserList);

        for (let i = 0; i < locationUserList.length; i++) {
            console.log(
                'user color   : ' +
                    locationUserList[i].wrm_user_color +
                    locationUserList[i].user_idx +
                    ' ' +
                    locationUserList[i].wrm_user_ready
            );

            const currentColor = locationUserList[i].wrm_user_color;

            colorList &&
                colorList.map((element) => {
                    if (element.color === currentColor) {
                        element.choose = 'false';
                        if (locationUserList[i].wrm_user_ready === true) {
                            ready_cnt += 1;
                            console.log('   : ' + ready_cnt);
                        }

                        if (save_user_idx == locationUserList[i].user_idx) {
                            //  
                            setSelectColor(element.color);
                            //  
                            setChangeReady(locationUserList[i].wrm_user_ready);
                        }
                    }
                    setColorList(colorList);
                });
        }

        //  
        console.log('      : ' + roomEnterInfo.room_current_member_cnt);
        setCurrentMember(roomEnterInfo.room_current_member_cnt);

        //   
        setStartMember(roomEnterInfo.room_start_member_cnt - 1);

        //  
        setLeaderIdx(roomEnterInfo.leader_idx);
    };

    useEffect(() => {
        // setRoomEnterInfo(location.state.data);

        settingRoom();

        setTimeout(() => getRoomInfo(), 1000); //   api +  
    }, []);

    return (
        console.log('   ' + JSON.stringify(userList)),
        console.log('    : ' + currentMember),
        console.log('  : ' + leaderIdx),
        (
            <Background>
                <Header />
                <Container>
                    {console.log('  ? : ' + isLeader)}
                    <SelectDiv>
                        selectDiv
                        <br />
                        {roomUpdate ? (
                            //      
                            <TitleDiv>
                                TitleDiv{result} {roomUpdate.room_idx} 
                                <br />
                                <Text>
                                     : {roomUpdate.room_name} |   : {roomEnterInfo.room_code} | :{' '}
                                    {roomEnterInfo.room_current_member_cnt} / {roomUpdate.room_start_member_cnt} 
                                </Text>
                                <br />
                                {isLeader === 1 ? (
                                    //   
                                    <ModalSetting
                                        resultt={result}
                                        clickedSetting={clickedSetting}
                                        title={roomUpdate.room_name}
                                        mode={roomUpdate.room_mode}
                                        member={roomUpdate.room_start_member_cnt}
                                        room_private={roomInfo.room_private}
                                        room_idx={roomUpdate.room_idx}
                                    />
                                ) : (
                                    <InfoModal
                                        title={roomUpdate.room_name}
                                        mode={roomUpdate.room_mode}
                                        member={roomUpdate.room_start_member_cnt}
                                        room_private={roomInfo.room_private}
                                        room_idx={roomUpdate.room_idx}
                                    />
                                )}
                            </TitleDiv>
                        ) : (
                            <TitleDiv>
                                TitleDiv {match.params.name} 
                                <br />
                                <Text>
                                     : {roomEnterInfo.room_name} |   : {roomEnterInfo.room_code} | :{' '}
                                    {roomEnterInfo.room_current_member_cnt} / {roomEnterInfo.room_start_member_cnt} 
                                </Text>
                                {isLeader === 1 ? (
                                    <ModalSetting
                                        //   
                                        resultt={result}
                                        clickedSetting={clickedSetting}
                                        title={roomInfo.room_name}
                                        mode={roomInfo.room_mode}
                                        member={count}
                                        room_private={roomInfo.room_private}
                                        room_idx={roomInfo.room_idx}
                                    />
                                ) : (
                                    <InfoModal
                                        //   
                                        title={roomInfo.room_name}
                                        mode={roomInfo.room_mode}
                                        member={count}
                                        room_private={roomInfo.room_private}
                                        room_idx={roomInfo.room_idx}
                                    />
                                )}
                            </TitleDiv>
                        )}
                        <BarDiv>
                            <BarInnerDiv>
                                {
                                    (console.log('  selectcolor : ' + selectColor),
                                    colorList &&
                                        colorList.map((element, key) =>
                                            //console.log('   ' + JSON.stringify(element.color)),
                                            //console.log(' ? : ' + element.choose),
                                            //console.log('   : ' + element.code),
                                            element.choose === 'true' ? (
                                                <BarColorBox
                                                    data={element.code}
                                                    color={element.code}
                                                    onClick={() => {
                                                        colorClick(element.color);
                                                    }}
                                                />
                                            ) : selectColor === element.color ? (
                                                <BarColorBox color={element.code}>V</BarColorBox>
                                            ) : (
                                                <BarColorBox data={element.code} color="#8C8C8C" />
                                            )
                                        ))
                                }
                            </BarInnerDiv>
                        </BarDiv>
                        <UserDiv>
                            <div style={styles.userListContainer}>
                                {userList &&
                                    userList.map((element) => (
                                        <UserCard
                                            leader={leaderIdx}
                                            id={element.user_idx}
                                            nickname={element.user_name}
                                            color={element.wrm_user_color}
                                            ready={element.wrm_user_ready}
                                        />
                                    ))}
                            </div>
                        </UserDiv>
                        <div
                            onClick={() => {
                                console.log('');
                                exitWaitingRoom();
                            }}
                            style={{
                                width: '100px',
                                justifyContent: 'space-between',
                                // backgroundColor: colors.red,
                            }}
                        >
                            <Exit src={exit} />
                            <ExitText style={{ color: colors.black }}></ExitText>
                        </div>
                    </SelectDiv>
                    <RightDiv>
                        <Chatting room_idx={location.state.data.room_idx} height="560px" available={true}></Chatting>
                        <StartDiv>
                            {
                                isLeader === 0 // 
                                    ? (console.log(style.red),
                                      changeReady === true ? (
                                          <BtnDiv
                                              color="green"
                                              onClick={() => {
                                                  readyClick(!changeReady);
                                              }}
                                          >
                                              Waiting...
                                          </BtnDiv>
                                      ) : (
                                          <BtnDiv
                                              onClick={() => {
                                                  readyClick(!changeReady);
                                              }}
                                          >
                                              Game Ready
                                          </BtnDiv>
                                      ))
                                    : //.
                                      (console.log(''),
                                      (
                                          //        
                                          // startMember === ready_cnt ? (
                                          <BtnDiv isStart="yes" onClick={startClick}>
                                              Game Start
                                          </BtnDiv>
                                      )) //  api  onclick 
                                // ) : (
                                // <BtnDiv isStart="no">Game Start</BtnDiv>
                                //)
                            }
                        </StartDiv>
                    </RightDiv>
                </Container>
            </Background>
        )
    );
}

const styles = {
    userListContainer: {
        display: 'flex',
        alignItems: 'flex-start',
        justifyContent: 'space-between',
        flexDirection: 'column',
        width: '590px',
        height: '410px',
        flexFlow: 'row wrap',
    },
};

const Background = styled.div`
    background-color: #180928;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const Container = styled.div`
    width: 1020px;
    height: 620px;
    // border: 1px solid #000;
    background-color: ${style.white};
    display: flex;
    flex-direction: row;
    overflow: hidden;
`;

const SelectDiv = styled.div`
    text-align: center;
    width: 800px;
    height: 620px;
    background-color: #ffe5e5;
    justify-content: center;
    align-items: center;
`;

const RightDiv = styled.div`
    width: 220px;
    height: 620px;
    overflow: hidden;
`;

const ChatDiv = styled.div`
    width: 220px;
    height: 560px;
    background-color: #ffe7a8;
    overflow: hidden;
`;

const StartDiv = styled.div`
    width: 220px;
    height: 60px;
    overflow: hidden;
`;

const BtnDiv = styled.div`
    width: 220px;
    height: 40px;
    margin-top: 10px;
    background-color: #ff0000;
    border-radius: 10px;
    box-shadow: 0px 3px 3px #878787;
    font-size: 25px;
    text-align: center;
    ${(props) => (props.color == 'green' ? `background-color: #44A024;` : ``)}
    ${(props) => (props.isStart == 'yes' ? `` : props.isStart == 'no' ? `opacity: 0.5;` : ``)}
`;
const TitleDiv = styled.div`
    width: 625px;
    height: 70px;
    margin-top: 25px;
    background-color: #ffffff;
    text-align: center;
    display: inline-block;
    overflow: hidden;
`;

const UserDiv = styled.div`
    width: 590px;
    height: 390px;
    margin-top: 10px;
    background-color: #fff3ca;
    display: inline-block;
    overflow: hidden;
    align-items: center;
    justify-content: center;
    text-align: center;
`;

const BarDiv = styled.div`
    width: 450px;
    height: 35px;
    //background-color: #6880fb;
    display: inline-block;
    overflow: hidden;
`;

const BarInnerDiv = styled.div`
    width: 420px;
    height: 29px;
    background-size: contain;
    margin-left: 15px;
    margin-top: 3px;
    //border-width:1;
    //border-style: solid;
    display: flex;
    flex-direction: row;
    border-radius: 30px;
    box-shadow: 0px 4px 4px #878787;
`;

const BarColorBox = styled.div`
    width: 70px;
    height: 29px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 2px solid #000000;
    disabled: true;

    ${(props) =>
        props.color == '#FF0000' || props.data == '#FF0000'
            ? `background-color: ${props.color}; border-top-left-radius: 15px; border-bottom-left-radius: 15px;`
            : props.color == '#FF00DD' || props.data == '#FF00DD'
            ? `background-color: ${props.color}; border-right: 0px solid #000000; border-top-right-radius: 15px; border-bottom-right-radius: 15px;`
            : `background-color: ${props.color};`}
`;

const Text = styled.text`
    color: #c11b1b;
    font-size: 12px;
    font-weight: light;
    // -webkit-text-stroke: 1px #c00202;
    margin-top: -5px;
`;

const Exit = styled.img`
    // position: absolute;
    // z-index: 0; //  30 ..
    width: 35px;
`;

const ExitText = styled.text`
    color: #c11b1b;
    font-size: 13px;
    font-weight: light;
    // -webkit-text-stroke: 1px #c00202;
    margin-top: -5px;

    &:hover {
        background-color: ${style.light_green};
        border: 1px solid ${style.white};
        color: ${style.red};
    }
`;
